// Gacha.js
import React, { useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import Shaker from "../components/Shaker"; // Shaker  import
import Rotate from "../components/Rotate"; //   import
import useParticles from "../components/Particles"; //    import
import {
  animateShakerTocatching,
  animateCatchingTransition,
  startBallAnimation,
} from "../components/Animation"; //    import

import "../css/gacha.scss";

function Gacha() {
  const navigate = useNavigate(); //    useNavigate
  const shakerRef = useRef(null); // Shaker  ref
  const catchingRef = useRef(null); // Catching  ref
  const ballRef = useRef(null); // (ball) ref
  const [showParticles, setShowParticles] = useState(false); //     state
  const { particlesComponent } = useParticles(); //   
  const [isBallClickable, setIsBallClickable] = useState(false); //      state

  //        
  const handleTransition = () => {
    setShowParticles(true); //  
    const tl = animateShakerTocatching(shakerRef, catchingRef, ballRef); //  
    tl.add(() => {
      startBallAnimation(catchingRef, ballRef, setIsBallClickable); //   
    }, ">"); //   
  };

  //         
  const handleBallClick = () => {
    if (isBallClickable) {
      setIsBallClickable(false); //    false 
      animateCatchingTransition(catchingRef, () => {
        navigate("/result"); //   
      });
    }
  };

  return (
    <>
      {/* Shaker  */}
      <div className="shaker full" ref={shakerRef}>
        <Shaker /> {/* Shaker   */}
        <div className="shaker-cover full">
          {/*     */}
          <div className="shaker-top-wall"></div>
          <div className="shaker-left-wall"></div>
          <div className="shaker-right-wall"></div>
          <div className="shake-window"></div>
          <div className="shaker-bottom-wall">
            {/*     */}
            {/* <div className="area-left">
              <h1>Gacha</h1>
              <h2>Turn the lever and pull out Sage!</h2>
            </div>
            <div className="area-right">
              <button className="btn-long"></button>
              <button className="btn-short"></button>
            </div> */}

            {/*  */}
            <div className="shaker-con-lever">
              <Rotate handleTransition={handleTransition} />{" "}
              {/*    */}
            </div>
          </div>
        </div>
      </div>
      {/*   */}
      {/*    */}
      <div
        className="catching full"
        ref={catchingRef}
        style={{ display: "none" }} //   
      >
        {showParticles && particlesComponent} {/*    */}
        <div className="catching-road">
          <div
            className="ball" // (ball) 
            style={{ transform: "translateY(-100vh)" }} //    
            ref={ballRef} //  ref
            onClick={handleBallClick} //     
          ></div>
          <h1>Touch Me!</h1> {/*    */}
        </div>
      </div>
    </>
  );
}

export default Gacha;
