// Animation.js
import { gsap } from "gsap";

export const animateShakerTocatching = (shakerRef, catchingRef, ballRef) => {
  const tl = gsap.timeline();

  // catching     
  tl.set(catchingRef.current, { display: "block", y: "100%" }) // catching    
    .to(shakerRef.current, { y: "-100%", duration: 1, ease: "power2.out" }) // Shaker   
    .to(catchingRef.current, { y: "0%", duration: 1, ease: "power2.out" }, 0) // catching   
    .set(shakerRef.current, { display: "none" }); // Shaker 

  return tl;
};

export const animateCatchingTransition = (catchingRef, callback) => {
  const tl = gsap.timeline();

  // catching     
  tl.set(catchingRef.current, { display: "block", scale: 1 }) // catching  
    .to(catchingRef.current, { scale: 3, duration: 1, ease: "power2.out" }) // catching 
    .call(callback); //    callback 

  return tl;
};

export const startBallAnimation = (
  catchingRef,
  ballRef,
  setIsBallClickable // setIsBallClickable  
) => {
  if (catchingRef.current) {
    catchingRef.current.style.display = "block";
    gsap.set(ballRef.current, { y: "-80vh", scale: 0.01, opacity: 0 });

    gsap.to(ballRef.current, {
      y: "-100%",
      scale: 1.8,
      opacity: 1,
      duration: 4,
      ease: "expoScale",
      delay: 1,
      onComplete: () => {
        const textElement = catchingRef.current.querySelector("h1");
        gsap.fromTo(
          textElement,
          { opacity: 0 },
          {
            opacity: 1,
            duration: 0.8,
            repeat: -1,
            yoyo: true,
            ease: "power1.inOut",
          }
        );
        setIsBallClickable(true); //   
      },
    });
  }
};
