import React, { useState, useEffect } from "react";
import { gsap } from "gsap";
import { Draggable } from "gsap/Draggable";

// Rotate  
const Rotate = ({ handleTransition }) => {
  // GSAP Draggable  
  gsap.registerPlugin(Draggable);

  //     
  const [rotation, setRotation] = useState(0);
  //     (4)
  const rotationSnap = 360 / 4;

  useEffect(() => {
    //   
    const knob = document.querySelector(".btn-lever");

    // Draggable  
    const draggable = Draggable.create(knob, {
      type: "rotation", //   
      bounds: { minRotation: 0, maxRotation: 270 }, //   
      snap: (endValue) => Math.round(endValue / rotationSnap) * rotationSnap, //  
      onDrag: function () {
        const newRotation = this.rotation; //    
        setRotation(newRotation); //  

        //   270   handleTransition 
        if (newRotation >= 270) {
          handleTransition(newRotation);
        }
      },
      onDragEnd: function () {
        const newRotation = this.rotation; //       

        //    270    0 
        if (newRotation < 270) {
          // 0    
          gsap.to(knob, { rotation: 0 });
          setRotation(0); //  0 
        }
      },
    });

    //    Draggable 
    return () => {
      draggable[0].kill();
    };
  }, [handleTransition]); // handleTransition   useEffect 

  return (
    <div
      className="btn-lever"
      style={{
        transform: `rotate(${rotation}deg)`, //   
      }}
    >
      <div className="btn-lever-rotate" />
    </div>
  );
};

export default Rotate;
